import { Box, styled, Button } from "@mui/material";
import React, { useState } from "react";
import { parseEther } from "@ethersproject/units";
import { ETH_VAL } from "../constants";
import { TitleFont } from "../components/Font/TitleFont";

export default function Account({
  address,
  userSigner,
  localProvider,
  mainnetProvider,
  price,
  minimized,
  web3Modal,
  loadWeb3Modal,
  logoutOfWeb3Modal,
  blockExplorer,
  contract, signer, remainTokenCount
}) {

  const [amount, setAmount] = useState(ETH_VAL);
  const [minting, setMinting] = useState(false);
  const [cnt, setCnt] = useState(1);
  const [currentAccount, setCurrentAccount] = useState(null);

  const mintNftHandler = async () => {
    setMinting(true);
    try {
      const mintFunction = contract.connect(signer)["mint"];
      console.log(mintFunction)
      const hash = await mintFunction(cnt, {
        value: parseEther((amount * cnt).toString()),
      });
      await hash.wait();
      setMinting(false);
    } catch (e) {
      setMinting(false);
      console.log(e);
    }
  };

  const modalButtons = [];
  if (web3Modal) {
    if (web3Modal.cachedProvider) {
      modalButtons.push(
        <Button
          key="logoutbutton"
          // style={{ verticalAlign: "top" }}
          onClick={mintNftHandler}
          sx={{
            border: "7px solid #626262",
            background: "transparent",
            borderRadius: "3rem",
            padding: "0px 40px",
          }}
          // className="connect_status light"
        >
          <TitleFont fontWeight="bold" size={50} color="#626262">
            Mint NFT
          </TitleFont>
        </Button>,
      );
    } else {
      modalButtons.push(
        <Button
          key="loginbutton"
          // style={{ verticalAlign: "top" }}
          /* type={minimized ? "default" : "primary"}     too many people just defaulting to MM and having a bad time */
          onClick={loadWeb3Modal}
          sx={{
            border: "7px solid #626262",
            background: "transparent",
            borderRadius: "3rem",
            padding: "0px 40px",
          }}
        >
        connect wallet
        </Button>,
      );
    }
  }

  return (
    <div>
      {modalButtons}
    </div>
  );
}
